import { useContext, useEffect } from "react";
import { Context } from "../App";
import { gsap } from "gsap";
import { ScrollTrigger, CSSPlugin, CSSRulePlugin } from "gsap/all";
import ParticlesBackground from "../components/ParticlesBackground";
import $ from 'jquery';
import "../styles/Home.css";

gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(CSSPlugin, CSSRulePlugin);

function Home () : JSX.Element {
    useEffect(() => {
        gsap.from(".floating", {y: 100, opacity: 0, duration: 1});
        gsap.from("#about h2, #about p", {opacity: 0, duration: 1, scrollTrigger: {
            trigger: "#about",
            start: "top center+=100"
        }});

        const chartl = gsap.timeline({
            scrollTrigger: {
                trigger: "#skills",
                start: "top center"
            }
        });

        var bars = [CSSRulePlugin.getRule("#f1:before"), CSSRulePlugin.getRule("#f2:before"), CSSRulePlugin.getRule("#f3:before")];

        chartl.from("#skills h2, #skills p, #chart, #icons", {opacity: 0, duration: 1}).from(bars, {cssRule: {"width": "0%"}, duration: 1}, "-=.5");

        gsap.from("#portfolio h2, #portfolio p, #portfolio .container", {opacity: 0, duration: 1, scrollTrigger: {
            trigger: "#portfolio",
            start: "top center"
        }});
        gsap.from("#contact h2, #contact p, #contact .container", {opacity: 0, duration: 1, scrollTrigger: {
            trigger: "#contact",
            start: "top center+=100"
        }});

        /* JQuery Code */
        $("#contactbtn").on("click", () : void => {
            $([document.documentElement, document.body]).animate({
                scrollTop: $("#contact").offset()?.top
            }, 1000);
        });

    }, []);

    const { state } = useContext(Context);

    return (
        <>
        <main>
            <section id="intro">
                <ParticlesBackground />
                <div className="floating">
                    <h2>{state.lang === "en" ? <><span>Hi</span><span>I'm Mateo,</span><span>Web developer</span></> : <><span>Hola</span><span>Soy Mateo,</span><span>Web developer</span></>}</h2>
                    <p>Front End / Wordpress</p>
                    <button id="contactbtn">{state.lang === "en" ? "Contact Me" : "Contáctame"}</button>
                </div>
                <div className="separator"></div>
                <div className="arrow">
                    <i className="fa-solid fa-angles-down"></i>
                </div>
            </section>
            <section id="about" className="home-sec underline">
                <h2>{state.lang === "en" ? "About Me" : "Sobre Mí"}</h2>
                <p>{state.lang === "en" ? 
                <>
                I'm a <strong>passionate</strong> Software Engineering student from <strong>Ecuador</strong> that likes to learn about new technologies and programming in general. I've always been an <strong>enthusiastic</strong>, <strong>ambitious</strong> and <strong>creative</strong> person, my love for art and computer science led me to this beautiful path of front end development. Most of my skills are self taught and I'm always open to new opportunities of learning and improving. 
                </>
                : <>
                Soy un <strong>apasionado</strong> estudiante de Ingeniería en Software de <strong>Ecuador</strong> que le gusta aprender sobre nuevas tecnologías y programación en general. Siempre he sido una persona <strong>entusiasta</strong>, <strong>ambiciosa</strong> y <strong>creativa</strong> así que mi amor al arte y a las ciencias de la computación me han guiado por este camino hermoso del desarrollo front end. La mayoría de mis habilidades son resultado de mi auto educación y siempre estoy abierto a nuevas oportunidades de aprender y mejorar.
                </>}</p>
            </section>
            <section id="skills" className="home-sec underline">
                <h2>{state.lang === "en" ? "Skills" : "Habilidades"}</h2>
                <p>{state.lang === "en" ? "I create successful websites that are fast, easy to use, and built with best practices. My main focus has been front end development, HTML, CSS, JS, building small and medium web apps." : "Yo creo paginas web exitosas que son rápidas, fáciles de usar, y diseñadas con las mejores prácticas. Mi enfoque principal ha sido el desarrollo front end, HTML, CSS, JS, construyendo web apps pequeñas y medianas."}</p>
                <div id="chart">
                    <div className="row">
                        <p>Front End</p>
                        <div id="f1" className="filler"></div>
                    </div>
                    <div className="row">
                        <p>Back End</p>
                        <div id="f2" className="filler"></div>
                    </div>
                    <div className="row">
                        <p>React JS</p>
                        <div id="f3" className="filler"></div>
                    </div>
                </div>
                <div id="icons">
                    <i className="fa-brands fa-html5"></i>
                    <i className="fa-brands fa-css3"></i>
                    <i className="fa-brands fa-js"></i>
                    <i className="fa-brands fa-react"></i>
                    <i className="fa-brands fa-git-alt"></i>
                    <i className="fa-brands fa-github"></i>
                    <i className="fa-brands fa-sass"></i>
                    <i className="fa-brands fa-bootstrap"></i>
                </div>
            </section>
            <section id="portfolio" className="home-sec underline">
                <h2>{state.lang === "en" ? "Projects" : "Proyectos"}</h2>
                <p>{state.lang === "en" ? "Some of the things I've worked on." : "Algunas de las cosas en las que he trabajado."}</p>
                <div className="container">
                    <a className="card" href="https://codepen.io/fmatharm/full/WNzZRPV" target="_blank" rel="noreferrer">
                        <p>25+5 Clock</p>
                        <img src={require("../media/projects/1.jpg")} alt="" />
                    </a>
                    <a className="card" href="https://codepen.io/fmatharm/full/vYRJKaz" target="_blank" rel="noreferrer">
                        <p>React Calculator</p>
                        <img src={require("../media/projects/2.jpg")} alt="" />
                    </a>
                    <a className="card" href="https://codepen.io/fmatharm/full/vYRxJax" target="_blank" rel="noreferrer">
                        <p>Drum Machine</p>
                        <img src={require("../media/projects/3.jpg")} alt="" />
                    </a>
                    <a className="card" href="https://codepen.io/fmatharm/full/NWYdaGV" target="_blank" rel="noreferrer">
                        <p>Markdown Previewer</p>
                        <img src={require("../media/projects/4.jpg")} alt="" />
                    </a>
                    <a className="card" href="https://codepen.io/fmatharm/full/abYpwyP" target="_blank" rel="noreferrer">
                        <p>Quote Machine</p>
                        <img src={require("../media/projects/5.jpg")} alt="" />
                    </a>
                </div>
            </section>
            <section id="contact" className="home-sec">
                <h2>{state.lang === "en" ? "Contact" : "Contacto"}</h2>
                <p>{state.lang === "en" ? <>Send me an Email: <strong>fm.armijosc@gmail.com</strong></> : <>Envíame un mensaje: <strong>fm.armijosc@gmail.com</strong></>}</p>
                <div className="container">
                <form action="https://formsubmit.co/fm.armijosc@gmail.com" method="POST">
                    <input type="text" name="name" placeholder={state.lang === "en" ? "Name" : "Nombre"} required/>
                    <input type="email" name="email" placeholder={state.lang === "en" ? "Your Email" : "Tu Email"} required/>
                    <input type="text" name="_subject"  placeholder={state.lang === "en" ? "Subject" : "Asunto"} required/>
                    <input type="text" name="_honey" style={{"display" : "none"}} />
                    <textarea name="message" placeholder={state.lang === "en" ? "Message" : "Mensaje"}></textarea>
                    <input type="hidden" name="_captcha" value="false"></input>
                    <button type="submit">{state.lang === "en" ? "Send" : "Enviar"}</button>
                </form>
                </div>
            </section>
        </main>
        <footer>
            <div className="separator-end"></div>
        </footer>
        </>
     );
}

export default Home;