import React from "react";
import Header from "./components/Header";
import Home from "./pages/Home";
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import { activateMenu, menuIsOff, deactivateMenu } from './animations/menu-animation';
import $ from 'jquery';

type AppState = {
    lang: string
}

type ContextState = {
    state: AppState,
    changeState: (param: AppState) => void;
}

export const Context = React.createContext <ContextState> ({
    state: {
        lang: 'en'
    },
    changeState: () : void => {}
});

class App extends React.Component <{}, AppState> {
    constructor (props: {}) {
        super (props);
        this.changeState = this.changeState.bind(this);
        this.state = {
            lang: 'en'
        }
    }

    changeState (signal: AppState) {
        this.setState (signal);
    }

    componentDidMount () {
        let myMenu = $("#menu");
        let myLinks = $(".links");

        myMenu.on('click', () : void => {
            if (menuIsOff) {
                activateMenu(myMenu);
                $('nav').css({
                    "transform" : "translateX(0)",
                    "opacity" : "1"
                });
                $('#external').css({
                    "transform" : "translateX(0)",
                    "opacity" : "1"
                });
            } else {
                deactivateMenu(myMenu);
                $('nav').css({
                    "transform" : "translateX(-100%)",
                    "opacity" : "0"
                });
                $('#external').css({
                    "transform" : "translateX(-100%)",
                    "opacity" : "0"
                });
            }
        });

        if (window.matchMedia("(max-width: 1024px)").matches) {
            myLinks.on("click", () : void => {
                deactivateMenu(myMenu);
                $('nav').css({
                    "transform" : "translateX(-100%)",
                    "opacity" : "0"
                });
                $('#external').css({
                   "transform" : "translateX(-100%)",
                    "opacity" : "0"
                });
            });
        }

        $(".links:first-of-type").on("click", () : void => {
            $([document.documentElement, document.body]).animate({
                scrollTop: $("#about").offset()!.top - 70
            }, 1000);
        });
        $(".links:nth-child(2)").on("click", () : void => {
            $([document.documentElement, document.body]).animate({
                scrollTop: $("#skills").offset()!.top - 70
            }, 1000);
        });
        $(".links:last-of-type").on("click", () : void => {
            $([document.documentElement, document.body]).animate({
                scrollTop: $("#portfolio").offset()!.top - 70
            }, 1000);
        });

        $("#title").on("click", () => {
            window.scrollTo(0, 0);
        });
    }

    componentDidUpdate () {
        console.log(this.state);
    }

    render () : JSX.Element {
        return (
            <Context.Provider value={{state: this.state, changeState: this.changeState}}>
                <BrowserRouter>
                    <Routes>
                        <Route path="/" element={<Header />}>
                            <Route index element={<Home />}/>
                        </Route>
                    </Routes>
                </BrowserRouter>
            </Context.Provider>
        );
    }
}

export default App;