var menuIsOff : boolean = true;

function activateMenu (eventTarget: JQuery) : void {
    menuIsOff = false;
    
    let lines : JQuery = eventTarget.children();

    lines.eq(0).css("transform", "translateY(12px)");
    lines.eq(2).css("transform", "translateY(-12px)");
    setTimeout(() => {
        lines.css({
            "position" : "absolute",
            "transition" : "none"
        });
        lines.eq(0).css("transform", "translateY(0)");
        lines.eq(2).css("transform", "translateY(0)");
    }, 300);
    setTimeout(() => {
        lines.eq(1).css("display", "none");
        lines.css("transition", "all 0.3s ease");
        lines.eq(0).css("transform", "rotate(45deg)");
        lines.eq(2).css("transform", "rotate(-45deg)");
    }, 350);
}

function deactivateMenu (eventTarget: JQuery) : void {
    menuIsOff = true;

    let lines : JQuery = eventTarget.children();

    lines.eq(0).css("transform", "rotate(0)");
    lines.eq(2).css("transform", "rotate(0)");
    setTimeout(() => {
        lines.eq(1).css("display", "block");
        lines.eq(0).css("transform", "translateY(12px)");
        lines.eq(2).css("transform", "translateY(-12px)");
    }, 300);
    setTimeout(() => {
        lines.eq(0).css("transform", "translateY(0)");
        lines.eq(2).css("transform", "translateY(0)");
        lines.css({
            "transition" : "none",
            "position" : "relative"
        });
        setTimeout(() => {
            lines.css("transition", "all 0.3s ease");
        }, 50);
    }, 600);
}

export { activateMenu, menuIsOff, deactivateMenu };