import React, { useContext } from 'react';
import { Context } from '../App';

function Traductor () : JSX.Element {

    const { state, changeState } = useContext(Context);

    const Traducir = (e: React.ChangeEvent<HTMLSelectElement>) => {
        changeState ({lang: e.target.value});
        document.documentElement.lang = e.target.value;
    }

    return ( 
        <div className="language">
            <label htmlFor="langbar">{state.lang === "en" ? "Change Language" : "Cambiar Idioma"}</label>
            <select id="langbar" onChange={Traducir} name="langbar">
                <option value="en">{state.lang === "en" ? "English" : "Inglés"}</option>
                <option value="es">{state.lang === "en" ? "Spanish" : "Español"}</option>
            </select>
        </div>
    );
}

export default Traductor;